}

.art-image-description {
  position: absolute;
  top:4px;
  left: 62px;
  font-size: 16px;
  color: #000;
  width: 100%;
  max-width: 314px;
}

.art-image {
  width: 100%; /* Ïù¥ÎØ∏ÏßÄ Í∞ÄÎ°ú ÌÅ¨Í∏∞ Ï°∞Ï†à */
  height: auto; /* ÎπÑÏú® Ïú†ÏßÄ */
  object-fit: cover;
  margin-top: 96px; /* Ïù¥ÎØ∏ÏßÄ ÏúÑÏπò Ï°∞Ï†ï */
}

/* ÎùºÎîîÏò§ Î≤ÑÌäº */
.art-radio {
  position: absolute;
  top: 24px;
  left: 24px;

}

/* Îã§Ïùå Î≤ÑÌäº */
.art-next-button {
  position: fixed;
  z-index: 1;  
  width: 100%;          /* ‚úÖ Î∂ÄÎ™® Ïª®ÌÖåÏù¥ÎÑàÍ∞Ä ÌôîÎ©¥ ÎÑàÎπÑ Ï†ÑÏ≤¥Î•º Ï∞®ÏßÄ */
  display: flex;        /* Î≤ÑÌäºÏùÑ Ï§ëÏïôÏóê ÎëêÎ†§Î©¥ flex ÏÇ¨Ïö© */
  justify-content: center;   /* Í∞ÄÎ°ú Ï§ëÏïô Ï†ïÎ†¨ */
  margin: 30vh ;
}

/* ÌåùÏóÖ Ï∞Ω */
.popup-message {
  position: fixed;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  color: #fff;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 14px;
  opacity: 0.9;
  z-index: 1000;
  animation: fadeInOut 2s ease forwards;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: clamp(16px, calc(20 / 412 * 100vw), 20px);
}

@keyframes fadeInOut {
  0% { opacity: 0; transform: translate(-50%, 20px); }
  10% { opacity: 0.9; transform: translate(-50%, 0); }
  90% { opacity: 0.9; }
  100% { opacity: 0; transform: translate(-50%, 20px); }
}

/* Î∞òÏùëÌòï */
@media (max-width: 412px) {
  .art-image-description {
    width: 274px;
  }
}
