.home {
  display: flex;
  flex-direction: column;
  padding-bottom: 70px;
  position: relative;
  width: 100%;
  max-width: 412px; /* PCÏóêÏÑú ÏµúÎåÄÌè≠ Ï†úÌïú */
  /* margin: 0 auto;  */
  overflow-x: hidden; /* PCÏóêÏÑú ÎÑòÏπòÎäî Í∞ÄÎ°ú ÏòÅÏó≠ Ïà®ÍπÄ */
  /* overflow-y: hidden; */

}

/* ===== ÏÉÅÎã® ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ===== */
.top-navbar {
  width: 100vw;
  max-width: 412px;
  height: 10vw;
  min-height: 48px;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 0px;
  background: linear-gradient(to bottom, rgb(255, 255, 255), rgba(255, 255, 255, 0.658), rgba(255, 255, 255, 0.0));
  z-index: 100;
}

.top-navbar img {
  width: 25px;
  height: 25px;
  cursor: pointer;
  margin-right: 3vw;
}

/* ===== Î∞∞ÎÑà ===== */
.banner {
  position: relative;
  width: 100vw;      /* Î∑∞Ìè¨Ìä∏ Ï†ÑÏ≤¥ Í∏∞Ï§Ä */
  height: 100vw;     /* Ï†ïÏÇ¨Í∞ÅÌòï ÎπÑÏú® Ïú†ÏßÄ */
    max-width: 412px;
  max-height: 424px;
  margin: 0;
  top: 0;
}

.banner-main {
  width: 100vw;
  height: 100vw;
    max-width: 412px;
  max-height: 424px;
  object-fit: cover;
  display: block;
}

/* Î∞òÌà¨Î™Ö Ïò§Î≤ÑÎ†àÏù¥ */
.banner-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.banner-overlay-img {
  width: 100vw;
  height: 100vw;
  max-width: 412px;
  max-height: 424px;
  object-fit: cover;
}

/* ÌÖçÏä§Ìä∏ÏôÄ Ïù∏ÎîîÏºÄÏù¥ÌÑ∞ */
.banner-texts {
  position: absolute;
  bottom: 0;         /* üëà ÌïòÎã®Ïóê Îî± Î∂ôÍ≤å */
  left: 0;
  width: 100%;
  z-index: 3;
  text-align: center;
  color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end; /* ÌïòÎã® Ï†ïÎ†¨ */
  padding-bottom: 0;         /* Ïó¨Î∞± Ï†úÍ±∞ */
}


.banner-texts .subtitle1 {
  padding: 0px 0px;
  margin: 0px 0px;
}

.banner-texts .text1 {
  padding: 0px 0px;
  margin: 0px 0px;

}

/* Ïù∏ÎîîÏºÄÏù¥ÌÑ∞ */
.banner-indicators {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin-top: 10px;
}

.indicator {
  width: 24px;
  height: 4px;
  border-radius: 2px;
  background-color: #cdcaca;
  cursor: pointer;
  transition: background-color 0.3s;
}

.indicator.active {
  background-color: #474444;
}

/* ===== ÏÑπÏÖò Í∏∞Î≥∏ ===== */
.home .section {
  /* padding-left: 20px; */
  padding-bottom: 30px;
  margin-left: 20px;
}
.home .section .subtitle1 {
  margin-top: 50px;
  margin-bottom: 25px;
}

/* ===== Ïä§ÌÅ¨Î°§ Í∞ÄÎ°úÌñâ (Í≥µÌÜµ) ===== */
.scroll-row {
  display: flex;
  gap: 10px;
  overflow-x: auto;
  scroll-snap-type: x mandatory; /* Ïä§ÏôÄÏù¥ÌîÑ Ïä§ÎÉÖ */
  -webkit-overflow-scrolling: touch;
  padding-bottom: 4px;
}
.scroll-row::-webkit-scrollbar {
  display: none;
}

/* ===== Monthly Card ===== */
.monthly-card {
  flex: 0 0 auto; /* Í∞ÄÎ°ú Ïä§ÏôÄÏù¥ÌîÑ Ïãú Ï§ÑÏñ¥Îì§ÏßÄ ÏïäÎèÑÎ°ù */
  width: 60vw;
  height: 15.27vw;
  max-width: 242px;
  max-height: 63px;
  background-color: #F0F0F5;
  border-radius: 10px;
  display: flex;
  align-items: center;
  overflow: hidden;
  padding: 0;
}
.monthly-card img {
  flex-shrink: 0;
  width: 15.27vw;
  height: 15.27vw;
  max-width: 70px;
  max-height: 70px;
  border-radius: 10px;
  object-fit: cover;
}
.card-info {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* ===== Recommend Card ===== */
.recommend-card {
  flex: 0 0 auto;
  width: 88vw;
  height: 46.12vw;
  max-width: 364px;
  max-height: 190px;
  border-radius: 10px;
  display: flex;
  background: #F0F0F5;
  overflow: hidden;
}

.recommend-card .poster {
  width: 32.04vw;
  height: 46.12vw;
  max-width: 132px;
  max-height: 190px;
  border-radius: 10px;
  object-fit: cover;
  flex-shrink: 0;
}

/* ‚úÖ Ïò§Î•∏Ï™Ω Ï†ïÎ≥¥ ÏòÅÏó≠ */
.recommend-info {
  flex: 1;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* ‚úÖ ÌÉúÍ∑∏ ÏòÅÏó≠ */
.tag-row {
  display: flex;
  gap: 2vw;
  margin-bottom: 26px;
  margin-right: 0;
}

.recommend-info .tag {
  width: auto;
  max-width: 126px;
  max-height: 32px;
  border: 1px solid #1D2087;
  color: #1D2087;
  font-size: 14px;
  font-weight: 500;
  border-radius: 8px;
  padding: 0 6px;
  margin: 0;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ‚úÖ ÌÖçÏä§Ìä∏ Í≥ÑÏ∏µ Íµ¨Ï°∞ */
.subtitle4 {
  /* font-weight: 600; */
  margin: 4px 0;
}

/* ===== Listen Card ===== */
.listen-card {
  flex: 0 0 auto;
  width: 41.26vw;  /* 170px Í∏∞Ï§Ä */
  height: 57.28vw; /* 236px Í∏∞Ï§Ä */
  max-width: 170px;
  max-height: 236px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}
.listen-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.overlay {
  position: absolute;
  inset: 0;
}
.play-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15vw;
  height: 15vw;
  max-width: 55px;
  max-height: 55px;
}

.listen-info {
  position: absolute;
  bottom: 0px;
}

.listen-info .text2 {
  margin: 0 0 0  10px;
  padding: 0;
}
.listen-info .subtitle3 {
  margin: 0 0 15px 0;
  padding: 0;
  
}

.progress-bar {
  position: absolute;
  bottom: 0px;
  left: 5px;
  width: 41.26vw; 
  height: 4px;
  border-radius: 2px;
  background-color: #F0F0F5;
}
.progress {
  width: 20vw; 
  height: 4px;
  border-radius: 2px;
  background-color: #6B6A6A;
}

/* ===== Event Image ===== */
.event-img {
  width: 90vw;
  height: 90vw;
  max-width: 364px;
  max-height: 364px;
}

