/* src\styles\RankingDocentCard.css */
.ranking-card {
  display: flex;
  align-items: stretch; /* ‚úÖ ÎÜíÏù¥Î•º ÏÑúÎ°ú ÎßûÏ∂îÍ∏∞ ÏúÑÌï¥ stretch ÏÇ¨Ïö© */
  gap: 3vw;
}

/* Ïù¥ÎØ∏ÏßÄ + Î≤àÌò∏ Î¨∂Ïùå */
.rank-image-wrap {
  position: relative;
  width: 30vw;
  max-width: 132px;
  aspect-ratio: 3 / 4;
  flex-shrink: 0; /* Ïù¥ÎØ∏ÏßÄ ÎπÑÏú® Ïú†ÏßÄ */
  display: flex;
  align-items: stretch; /* ÎÜíÏù¥ ÎèôÏùºÌïòÍ≤å ÎßûÏ∂îÍ∏∞ */
}

/* Ìè¨Ïä§ÌÑ∞ Ïù¥ÎØ∏ÏßÄ */
.rank-poster {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

/* ÏàúÏúÑ Î≤àÌò∏ (Ìè¨Ïä§ÌÑ∞ ÏúÑ Ï¢åÏ∏° ÌïòÎã® Î∞∞Ïπò) */

.ranking-card {
  margin-left: 10px;
}

.rank-number {
  position: absolute;
  left:-10px; /* Ï¢åÏ∏° Ïó¨Î∞± */
  bottom: 0px; /* ÌïòÎã® Ïó¨Î∞± */
  color: #000000;
  font-family: 'Noto Serif', sans-serif;
  font-weight: 700;
  font-size: clamp(36px, calc(48 / 360 * 100vw), 48px);
  z-index: 2;
}

/* Ïò§Î•∏Ï™Ω Ï†ïÎ≥¥ ÏòÅÏó≠ */
.rank-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between; 
}

.ranking-list .tag-wrap {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.ranking-list .tag {
  width: auto;
  max-width: 128px;
  border: 2px solid #1D2087;
  border-radius: 8px;
  padding: 4px 8px;
  font-size: clamp(12px, calc(14 / 360 * 100vw), 14px);
}

.lisen {
  width: 19px;
  height: 19px;
  margin-right: 4px;
  vertical-align: middle;
}

.view-count {
  font-size: 12px;
  color: #1D2087;
  display: flex;
  align-items: center;
}

.rank-info .subtitle3{
  margin: 0;
  max-width: 170px;
}
.rank-info .text2 {
  margin: 6px 3px 0px 0px;
}
