/* src\styles\NavbarBottom.css */

.navbar-bottom {
  position: fixed;
  bottom: 0;
  width: 100vw;
  max-width: 412px;
  height: 74px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.4); /* Î∞∞Í≤Ω 1% Î∂àÌà¨Î™ÖÎèÑ */
  backdrop-filter: blur(15px); /* background blur */
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3); 
  border-top: 2px solid rgba(255, 255, 255, 0.6); /* ÏúÑÏ™Ω inside stroke */
  border-left: 2px solid rgba(255, 255, 255, 0.6);
  border-right: 2px solid rgba(255, 255, 255, 0.6);
  border-top-left-radius: 25px;   /* ÏôºÏ™Ω ÏúÑ Îë•Í∏ÄÍ≤å */
  border-top-right-radius: 25px;  /* Ïò§Î•∏Ï™Ω ÏúÑ Îë•Í∏ÄÍ≤å */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.navbar-content {
  display: flex;
  gap: 28px; /* ÏïÑÏù¥ÏΩò/ÌÖçÏä§Ìä∏ Î¨∂Ïùå Í∞ÑÍ≤© */
  align-items: center;
  justify-content: center;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}

.nav-icon {
  width: 32px; /* ÏïÑÏù¥ÏΩò ÌÅ¨Í∏∞ Ï°∞Ï†à */
  height: 32px;
  object-fit: contain;
}

.nav-label {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 500;
  font-size: 10px;
  color: #CDCACA;
  margin-top: 2px;
  text-align: center;
}

/* ÌôúÏÑ± ÏÉÅÌÉú */
.nav-item.active .nav-label {
  color: #474444;
}
